import request from '@/utlis/request'

// 兼职列表查询
export  const getProjectList = () =>{
    return request({
        method:'GET',
        url:'plan',
        params:{
            type:'plist'
        }
    })
}

// 添加兼职单
export const AddProject = (data) =>{
    return request({
        method:'POST',
        url:'plan',
        data
    })
}

// 编辑兼职单
export const EditProject = (data) =>{
    return request({
        method:'PUT',
        url:'plan',
        data
    })
}

// 搜索
export const SearchPlan = (data) => {
    return request({
        method:'GET',
        url:'plan',
        params:{...data}
    })
}

// 删除兼职单
export const DeleteProject = (data) =>{
    return request({
        method:'DELETE',
        url:'plan',
        data
    })
}

// 确认接单
export const Sure_jianzhi_order = (data) =>{
    return request({
        method:'POST',
        url:'student',
        data
    })
}
