<template>
    <div>
        <!-- 订单头部-->
        <div slot="header" class="clearfix">
                <el-button style="float: left;" type="primary" icon="el-icon-circle-plus-outline" @click="handleAdd">新增订单</el-button>
                <el-form :inline="true" :model="projectForm" class="demo-form-inline">
                    <el-form-item label="">
                        <el-input  class="project_input" v-model="projectForm.project_name" placeholder="输入需求名称搜索"></el-input>
                    </el-form-item>
                    <el-form-item>
                        <el-button class="project_search" type="primary" icon="el-icon-search" @click="handleSearch">搜索</el-button>
                    </el-form-item>
                    <el-form-item>
                        <el-button class="project_refresh" type="primary" icon="el-icon-refresh" @click="handleRefresh">刷新</el-button>
                    </el-form-item>
                </el-form>
        </div>

        <!-- 订单主体 -->
        <el-table
            :data="tableData.slice((currentPage-1)*pagesize,currentPage*pagesize)"
            style="width: 100%">
          <el-table-column
              prop="jz_order_num"
              label="订单编号"
              width="200">
          </el-table-column>
          <el-table-column
              prop="project_name"
              label="需求名称"
              width="200">
          </el-table-column>
          <el-table-column
              prop="description"
              label="需求描述"
              width="500">
          </el-table-column>
          <el-table-column
              prop="cycle_requiremente"
              label="周期要求"
              width="80">
          </el-table-column>
          <el-table-column
              prop="technical_requiremente"
              label="技术要求"
              width="200">
          </el-table-column>
          <el-table-column
              prop="creat_time"
              label="发布时间"
              width="120">
          </el-table-column>
          <el-table-column
              prop="budget"
              label="预算"
              width="120">
          </el-table-column>
          <el-table-column
              label="操作栏"
              width="200">
            <template slot-scope="scope">
              <el-button type="warning" @click.stop="handleEdit(scope.row)">编辑</el-button>
              <el-button type="danger" @click.stop="handelDel(scope.row)">删除</el-button>
            </template>
          </el-table-column>
        </el-table>

        <!--订单新增/编辑表单-->
        <el-dialog title="项目信息" :visible.sync="dialogFormVisible">
            <el-form :model="projectInFo">
                <el-form-item label="需求名称" :label-width="formLabelWidth">
                    <el-input v-model="projectInFo.project_name" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="需求详细描述" :label-width="formLabelWidth">
                    <el-input v-model="projectInFo.description" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="周期要求" :label-width="formLabelWidth">
                    <el-input v-model="projectInFo.cycle_requiremente" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="技术要求" :label-width="formLabelWidth">
                    <el-input v-model="projectInFo.technical_requiremente" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="预算" :label-width="formLabelWidth">
                    <el-input v-model="projectInFo.budget" autocomplete="off"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogFormVisible = false">取 消</el-button>
                <el-button v-if="isAdd" type="primary" @click="processAdd">新增</el-button>
                <el-button v-if="!isAdd" type="primary" @click="processEdit">编辑</el-button>
            </div>
        </el-dialog>

        <!-- 分页 -->
        <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="currentPage"
            background
            :page-sizes="[20,30,50]"  
            :page-size="pagesize"         
            layout="total, sizes,  prev, pager, next, jumper" 
            :total="tableData.length">
        </el-pagination>
    </div>
</template>

<script>
import {getProjectList,AddProject,DeleteProject,EditProject,SearchPlan} from "@/services/project"
export default {
    name:'Project',
    data(){
        return{
            tableData: [],
            isAdd:true,
            currentId:null,
            dialogFormVisible: false,
            projectForm: {
                project_name: ''
            },
            projectInFo: {
                project_name: '',
                description: '',
                cycle_requiremente: '',
                technical_requiremente: '',
                budget: ''
            },
            formLabelWidth: '120px',
            totalCount:0,
            currentPage:1, // 初始页
            pagesize:20,  // 初始每页的数据
        }
    },
    created() {
        this.loadProjectInfo()
    },
    methods:{
        // 初始页currentPage、初始每页数据数pagesize和数据data
        handleSizeChange: function (size) {
                this.pagesize = size;
                // console.log(this.pagesize)  //每页下拉显示数据
        },
        handleCurrentChange: function(currentPage){
                this.currentPage = currentPage;
                // console.log(this.currentPage)  //点击第几页
        },
        // 列表
        async loadProjectInfo(){
            try {
                const {data:ret} = await getProjectList()
                this.tableData = ret.data
                this.totalCount = ret.total_count    // 取出接口返回的兼职总数
            }
            catch (error) {
                if (error.message === '未登录') {
                    this.$router.push('/login') // 跳转至登录页
                } else {
                    // 处理其他错误
                    this.$message.success('登录失效，请重新登陆')
                    this.$router.push('/login') // 跳转至登录页
                }
            }
        },
        // 触发新增
        handleAdd(){
            this.dialogFormVisible = true
            this.isAdd = true
        },
        // 执行新增
        async processAdd(){
            const addOption = {
                project_name:this.projectInFo.project_name,
                description:this.projectInFo.description,
                cycle_requiremente:this.projectInFo.cycle_requiremente,
                technical_requiremente:this.projectInFo.technical_requiremente,
                budget:this.projectInFo.budget
            }
            const {data:res}=await AddProject(addOption)
            // 判断请求结果，处理表单是否关闭
            if (res.code==='0'){
                // 提示添加成功，隐藏表单，并刷新列表
                this.$message.success('添加成功')
                this.dialogFormVisible = false
                this.loadProjectInfo()
                this.projectInFo = {
                    project_name: '',
                    description: '',
                    cycle_requiremente:'',
                    technical_requiremente:'',
                    budget:'',
                    type: null
                }
            }
        },
        // 删除
        handelDel(rowData){
            this.$confirm('删除兼职, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(async () => {
                const {data:res} = await DeleteProject({
                    id:rowData.id
                })
                if (res.code==='0'){
                    this.$message.success('删除成功')
                    this.loadProjectInfo()
                }
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消'
                })
            })
        },
        // 触发编辑
        handleEdit(rowData){
            this.currentId = rowData.jz_order_num
            this.isAdd = false
            this.dialogFormVisible = true
            this.projectInFo.project_name = rowData.project_name
            this.projectInFo.description = rowData.description
            this.projectInFo.cycle_requiremente = rowData.cycle_requiremente
            this.projectInFo.technical_requiremente = rowData.technical_requiremente
            this.projectInFo.budget = rowData.budget
        },
        // 执行编辑
        async processEdit(){
            const EditOption = {
                id:this.currentId,
                project_name:this.projectInFo.project_name,
                description:this.projectInFo.description,
                cycle_requiremente:this.projectInFo.cycle_requiremente,
                technical_requiremente:this.projectInFo.technical_requiremente,
                budget:this.projectInFo.budget
            }
            const {data:res} = await EditProject(EditOption)
            if (res.code === '0'){
                this.$message.success('修改成功')
                this.dialogFormVisible = false
                this.projectInFo = {
                    project_name: '',
                    description: '',
                    cycle_requiremente:'',
                    technical_requiremente:'',
                    budget:'',
                    type: null
                }
                this.loadProjectInfo()
            }
        },
        // 搜索
        async handleSearch() {
            const searchOptions = {
                project_name:this.projectForm.project_name,
                type:'pname'
            }
            const {data:res} = await SearchPlan(searchOptions)
            if (res.code==='0'){
                this.tableData = res.data
                this.projectForm.project_name = ''
            }
        },
        // 刷新
        handleRefresh(){
            this.projectForm.project_name=''
            this.loadProjectInfo()
        }
    }
}
</script>

<style lang="scss" scoped>
.project_input{
  margin: 0 20px;
}
.project_refresh{
  margin: 0 2px;
}
.project_search{
  margin: 0 28px;
}
.clearfix{
  height: 55px;
}
</style>
